@import url(https://fonts.googleapis.com/css2?family=EB+Garamond&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #002460;
  background: url(/drew-bradley-technical-resume/static/media/FullDesk.1c382c5b.jpg) no-repeat center center fixed;
  background-size: cover;
  background-repeat: no-repeat;
  color: black;
  height: 100vh;
}

p {
  /* font-size: 1.25rem; */
}


@media screen and (max-width: 600px) {
  p {
    font-size: 1rem;
  }
}
.App {
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 40% 60%;
  margin: auto;
  width: 80%;
}

.title {
  font-family: 'EB Garamond', Times, serif;
  text-align: center;
  text-shadow: 1px 3px 10px rgba(255,255,255,0.68);
}

.title-name {
  font-size: 7rem;
  margin-top: 2%;
  margin-bottom: 0;
}

@media screen and (max-width: 600px){
  .App {
    width: 100%;
    grid-template-rows: 40% 60%;
  }

  .title-name {
    font-size: 4rem;
  }
}

@media screen and (max-width: 1000px) and (min-width: 600px) {
  .App {
    width: 90%;
  }
}
.header-container {
  color: black;
  background-color: hsla(0, 0%, 96%, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  grid-area: 1 / 1 / 2 / 3;
}

.contact, nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.navbar {
  text-decoration: none;
  background-color: whitesmoke;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.nav {
  padding: 2%;
  margin: 1% auto;
  font-weight: 500;
  border-radius: 5px;
  color: black;
  transition: 200ms;
}

.nav:hover {
  background-color: rgba(200, 219, 228, 0.705);
  box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.5);
  transition: 200ms;
}

.home {
  background-color: rgb(145, 145, 145);
  color: black;
}

.edu {
  background-color: rgb(210, 210, 210);
  color: black;
}

.projects {
  background-color: whitesmoke;
  color: black;
}

.history {
  background-color: rgb(172, 171, 171);
  color: black;
}

a {
  text-decoration: none;
  color: black;
}

.contact {
  flex-direction: column;
  align-items: center;
  background-color: hsla(0, 0%, 96%, 0.2);
}

.contact-form {
  background-color: hsla(0, 0%, 96%, 0.8);
  border: 1px solid black;
  width: 80%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.contact-form input {
  border: 1px solid black;
  height: 1.5rem;
}

.contact-form textarea {
  border: 1px solid black;
}

.contact-form button {
  background-color: hsla(0, 0%, 96%, 0.8);
  border: 1px solid black;
  padding: 2%;
  margin: 1% auto;
  font-weight: 500;
  color: black;
  transition: 200ms;
}

.contact-form button:hover {
  background-color: rgba(200, 219, 228, 0.705);
  box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.5);
  transition: 200ms;
}

.contact-deet {
  margin: 0%;
  padding: 0%;
}

@media screen and (max-width: 600px) {
  .contact {
    flex-direction: column;
    align-items: center;
  }
  .contact-form {
    width: 100%;
    height: 100%;
  }
  .contact-deet {
    margin: 0%;
    padding: 0%;
  }
}
.workplace {
  text-align: left;
  margin: 2%;
  padding: 1%;
  border: black solid 1px;
  background-color: hsla(0, 0%, 96%, 0.8);
}
.history-container {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 100%;
  background-color: hsla(0, 0%, 96%, 0.2);
}

/* @media screen and (max-width: 600px) {
  } */
.project {
  background-color: hsla(0, 0%, 96%, 0.8);
  text-align: left;
  margin: 1%;
  padding: 1%;
  border: black 1px solid;
  display: grid;
  grid-template-columns: 25% 75%;
  grid-template-rows: 60% 40%;
}

.project-container {
  display: grid;
  margin: auto;
  width: 100%;
  grid-template-columns: auto;
  grid-row: repeat(4, 1fr);
  background-color: hsla(0, 0%, 96%, 0.2);
}

.project-name {
  text-align: center;
  margin: 0;
  font-size: 1.5rem;
}

.project-deets {
  grid-area: 1 / 2 / 3 / 3;
  margin: 1%;
  display: flex;
  flex-direction: column;
}

.project-gif {
  width: auto;
  margin-top: -50px;
}

.project-gif-container {
  text-align: center;
  height: 15rem;
  grid-area: 1 / 1 / 4 / 2;
  overflow: hidden;
}

.project-links {
  border: 1px solid black;
  display: flex;
  justify-content: space-around;
  margin: auto;
  width: 100%;
  grid-area: 2 / 2 / 3 / 3;
}

@media screen and (max-width: 600px) {
  .project {
    grid-template-columns: 40% 60%;
    grid-template-rows: 80% 20%;
  }

  .project-gif {
    height: 300px;
    margin-top: -40px;
  }

  .project-gif-container {
    height: 275px;
    grid-area: 1 / 1 / 2 / 2;
  }

  .project-deets {
    grid-area: 1 / 2 / 2 / 3;
  }

  .project-desc {
    overflow: scroll;
  }

  .project-links {
    grid-area: 2 / 1 / 2 / 3;
  }

}
.skills {
  text-align: center;
  background-color: hsla(0, 0%, 96%, 0.2);
  width: 100%;
  /* height: auto; */
  /* min-height: 100%; */
}

.tech-container {
  margin: 2%;
  display: flex;
  flex-direction: column;
  border: solid 1px black;
  background-color: hsla(0, 0%, 96%, 0.8);
}

.tech-box {
  display: flex;
  justify-content: space-around;
  padding: 1%;
}

.tech-name {
  margin: 0 auto 0 5%;
}

.tech-logo {
  height: 50px;
}

.edu-container {
  background-color: hsla(0, 0%, 96%, 0.8);
}

.school {
  padding: 2%;
  margin: 2%;
  display: grid;
  grid-template-columns: auto;
  grid-row: 20% 5% 55%;
  border: solid 1px black;
}

.school-name {
  margin: 0;
  grid-area: 1/ 1 / 2/ 3;
}

.school-dates {
  margin: 1%;
  grid-area: 2/ 1 / 3/ 3;
}

.school-details {
  grid-area: 3/ 1 / 3/ 3;
}

@media screen and (max-width: 600px) {
  .tech-box {
    display: grid;
    margin: 2%;
    grid-template-columns: repeat(2, 1fr);
  }

  .skills {
    /* height: 100vh; */
    /* min-height: 100%; */
  }
}

@media screen and (max-width: 1000px) and (min-width: 600px) {
  .skills {
    /* height: 80vh;
    min-height: 100%; */
  }
}
.Main {
  display: grid;
  margin: auto;
  width: 100%;
  min-height: 100%;
  grid-template-columns: auto;
  grid-row: repeat(4, 1fr);
  background-color: hsla(0, 0%, 96%, 0.2);
  /* background-color: rgb(145, 145, 145); */
}

.BioCard {
  display: grid;
  margin: auto;
  width: 95%;
  /* grid-template-columns: auto; */
  grid-template-columns: repeat(40% 60%);
}

.headshot {
  height: 300px;
  margin: 5%;
  /* border: 3px solid black; */
  border-radius: 50%;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.81);
}

article {
  margin: 2%;
  padding: 2%;
  background-color: whitesmoke;
  border: 1px solid black;
  grid-area: 1 / 2 / 3 / 3;
}

@media screen and (max-width: 600px) {
  .BioCard {
    grid-template-columns: 100%;
    grid-template-rows: repeat(2, 1fr);
  }

  .headshot {
    width: 90%;
    height: auto;
  }

  article {
    grid-area: 2 / 1 / 3 / 2;
  }
}
